<template>
  <section class="services">
    <app-title text="Serviços" />
    <ul class="service-types d-flex pa-0 mt-2">
      <li class="service-type pa-2">
        <v-chip
          :class="activeClass(null)"
          class="primary--text"
          @click="chooseServiceType(null)"
        >
          Todos
        </v-chip>
      </li>
      <li
        v-for="serviceType in serviceTypes"
        :key="serviceType.id"
        class="service-type pa-2"
      >
        <v-chip
          :class="activeClass(serviceType.id)"
          class="primary--text"
          @click="chooseServiceType(serviceType.id)"
        >
          {{ serviceType.name }}
        </v-chip>
      </li>
    </ul>
    <ul class="services-list mt-4" v-if="servicesCmpt.length > 0">
      <li
        v-for="service in servicesCmpt"
        :key="service.id"
        class="service px-0 px-sm-4"
      >
        <v-row align="center">
          <v-col>
            <h3 class="title primaryLight--text ma-0">
              {{ service.name }}
            </h3>
            <p class="description primaryLight--text ma-0"></p>
          </v-col>
          <v-spacer class="d-none d-sm-flex"></v-spacer>
          <v-col cols="auto" class="text-right px-0 px-sm-3">
            <span class="price d-block primaryLight--text">
              R$ {{ setMoneyMask(service.price) }}
            </span>
            <span class="time d-block primaryLight--text">
              {{ service.duration_time | timeToText }}
            </span>
          </v-col>
          <v-col cols="auto">
            <v-btn
              class="btn-schedule success text-none primaryLight--text elevation-0"
              @click="schedule(service.id)"
            >
              Reservar
            </v-btn>
          </v-col>
        </v-row>
      </li>
    </ul>
    <p class="mt-4 text-center text-sm-left" v-else>
      A barbearia não possui serviços para esta categoria
    </p>
  </section>
</template>

<script>
import AppTitle from '@/components/site/Title'

import TextMixin from '@/mixins/TextMixin'
import TimeMixin from '@/mixins/TimeMixin'

export default {
  components: { AppTitle },
  mixins: [ TextMixin, TimeMixin ],
  props: {
    barbershopId: { required: true }
  },
  data: () => ({
    services: [],
    serviceType: null,
    serviceTypes: []
  }),
  computed: {
    servicesCmpt () {
      let services = [...this.services]
      if (this.serviceType) {
        services = services.filter(service => Number(service.service_type_id) === Number(this.serviceType))
      }
      return services
    }
  },
  mounted () {
    this.getServiceTypes()
    this.getServices()
  },
  methods: {
    activeClass (serviceType) {
      return serviceType === this.serviceType ? 'success' : ''
    },
    chooseServiceType (serviceTypeId) {
      this.serviceType = serviceTypeId
    },
    getServices () {
      this.$axios(`${this.$url_api}/service/barbershop/${this.barbershopId}?paginate=false`)
      .then(response => {
        if (Number(response.status) === 200) {
          if (Number(response.data.codigo) === 200) {
            this.services = response.data.corpo
          }
        }
      })
    },
    getServiceTypes () {
      this.$axios(`${this.$url_api}/service-types`)
      .then(response => {
        if (Number(response.status) === 200) {
          if (Number(response.data.codigo) === 200) {
            this.serviceTypes = response.data.corpo
          }
        }
      })
    },
    schedule (serviceId) {
      this.$emit('schedule', serviceId)
    }
  }
}
</script>

<style scoped>
.services-list { max-height: 500px; overflow: hidden; overflow-y: auto; padding: 0 !important; }
.service { border-bottom: 1px solid #707070; list-style: none; }
.title { font-size: 1rem !important; font-weight: 600; line-height: 1.5rem; }
.price, .description, .time { font-size: .9rem; }
.price { font-weight: 600; }
.btn-schedule { width: 180px; border-radius: 50px; height: 40px; }
.service-types { overflow: hidden; overflow-x: auto; width: 100%; }
.service-type { list-style: none; cursor: pointer; }
@media all and (max-width: 599px) {
  .btn-schedule, .price, .description, .time, .title { font-size: .8rem !important; }
  .btn-schedule { width: 80px; }
}
</style>