<template>
  <div class="barbershop-contact">
    <v-row>
      <v-col v-if="false" cols="12" class="text-right d-none d-sm-flex">
        <a :href="shareUrl" class="btn-link btn-share">
          <span class="label gray3--text">Compartilhe</span>
          <v-icon class="ml-2">share</v-icon>
        </a>
      </v-col>
      <v-col>
        <label class="label gray3--text d-block mb-1">
          Contatos e horários
        </label>
        <a class="btn-link" :href="'tel:' + phoneNumber">
          <v-icon class="mr-2">phone_iphone</v-icon>
          <span class="label gray3--text">
            {{ setPhoneMask(phoneNumber) }}
          </span>
        </a>
      </v-col>
      <v-col cols="auto">
        <label class="label gray3--text d-block mb-1">
          Mídias sociais
        </label>
        <div class="d-flex justify-space-around">
          <a v-if="facebookUrl" :href="facebookUrl" target="_blank" class="btn-link">
            <facebook class="social" />
          </a>
          <a v-if="instagramUrl" :href="instagramUrl" target="_blank" class="btn-link">
            <instagram class="social" />
          </a>
        </div>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import Facebook from '@/components/icons/Facebook'
import Instagram from '@/components/icons/Instagram'

import TextMixin from '@/mixins/TextMixin'

export default {
  components: { Facebook, Instagram },
  mixins: [ TextMixin ],
  props: {
    facebookUrl: { default: null },
    instagramUrl: { default: null },
    phoneNumber: { default: null }
  },
  computed: {
    shareUrl () {
      return 'https://api.whatsapp.com/send?text=www.google.com'
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@/scss/Theme.scss';

.label {
  font-weight: 400;
  font-size: .9rem;
}

.btn-link {
  display: flex;
  text-decoration: none !important;
  align-items: center;
  &:hover {
    .label {
      color: $primaryDark !important;
      font-weight: 500;
    }
    
    i {
      color: $primaryDark !important;
    }
  }
}

.social { height: 30px; }

.btn-share { justify-content: end; }
</style>
