<template>
  <div class="barbershop-image">
    <div class="image-content elevation-1">
      <img :src="image" />
    </div>
  </div>
</template>

<script>
export default {
  props: {
    imageUrl: { required: true }
  },
  computed: {
    image () {
      return `${this.$url_backend}${this.imageUrl}`
    }
  }
}
</script>

<style lang="scss">
.image-content { overflow: hidden; border-radius: 5px;
  img { width: 100%; display: block; }
}
</style>