<template>
  <transition
    enter-active-class="animated fadeIn"
    leave-active-class="animated fadeOut"
    appear
  >
    <div v-if="loading" class="loader-box">
      <div class="loader-content">
        <v-progress-circular :size="70" :width="2" indeterminate></v-progress-circular>
        <span
          v-show="!noLoadingText"
          class="d-block text-center primary--text mt-4"
        >
          {{ loadingText }}
        </span>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  props: {
    loadingText: { default: 'Carregando...' },
    noLoadingText: { default: false }
  },
  data: () => ({
    loading: false,
    loadingTimeOut: 1000
  }),
  methods: {
    setLoading (loading, timeout) {
      setTimeout(() => {
        this.loading = loading
      }, timeout);
    },
    show () {
      this.setLoading(true, 0)
    },
    hide () {
      this.setLoading(false, this.loadingTimeOut)
    }
  }
}
</script>

<style lang="scss" scoped>
.loader-box {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  overflow: hidden;
  &:before {
    content: '';
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: #fff;
    opacity: .8;
  }
}
.loader-content {
  position: relative;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
</style>