<template>
  <section class="schedules px-4">
    <article
      v-for="schedule in schedulesCmpt"
      :key="schedule.dayOfWeek"
      class="schedule-item py-2"
      :class="{ active : schedule.active }"
    >
      <h1 class="ma-0 gray3--text">{{ schedule.text }}</h1>
      <div class="primaryLight--text">
        <template v-if="schedule.isOpen">
          {{ schedule.startTime | time }} - {{ schedule.endTime | time }}
        </template>
        <span v-else>Fechado</span>
      </div>
    </article>
  </section>
</template>

<script>
import DateMixin from '@/mixins/DateMixin'

export default {
  mixins: [ DateMixin ],
  props: {
    scheduleDays: { default: [] }
  },
  data: () => ({
    schedules: [
      { text: 'Segunda-feira', dayOfWeek: 0, isOpen: false, startTime: null, endTime: null },
      { text: 'Terça-feira', dayOfWeek: 1, isOpen: false, startTime: null, endTime: null },
      { text: 'Quarta-feira', dayOfWeek: 2, isOpen: false, startTime: null, endTime: null },
      { text: 'Quinta-feira', dayOfWeek: 3, isOpen: false, startTime: null, endTime: null },
      { text: 'Sexta-feira', dayOfWeek: 4, isOpen: false, startTime: null, endTime: null },
      { text: 'Sábado', dayOfWeek: 5, isOpen: false, startTime: null, endTime: null },
      { text: 'Domingo', dayOfWeek: 6, isOpen: false, startTime: null, endTime: null }
    ]
  }),
  computed: {
    schedulesCmpt () {
      const schedules = [...this.schedules]
      const date = new Date()
      const dayOfWeek = date.getDay()
      schedules.map(schedule => {
        schedule['active'] = schedule.dayOfWeek === dayOfWeek  
      })
      this.scheduleDays.forEach(s => {
        const index = schedules.findIndex(schedule => schedule.dayOfWeek === s.schedule_day_id)
        
        if (index > -1) {
          let schedule = schedules[index]
          schedule.isOpen = s.open
          schedule.startTime = s.start
          schedule.endTime = s.end
          schedules[index] = schedule
        }
      })
      return schedules
    }
  }
}
</script>

<style lang="scss" scoped>
.schedule-item { 
  display: flex;
  justify-content: space-between;
  font-size: .8rem;
  &.active > * {
    font-weight: 600;
  }
}

h1 { font-weight: 400; }
</style>