<template>
  <section class="members">
    <app-title text="Membros da equipe" />
    <ul v-if="members.length > 0" class="pa-0">
      <li 
        v-for="member in members"
        :key="member.id"
        class="member pa-4"
      >
        <div class="image-box gray1 elevation-1">
          <img v-if="member.image_url" :src="getImage(member.image_url)" />
        </div>
        <h3 class="gray3--text mt-4">
          {{ member.name }}
        </h3>
      </li>
    </ul>
    <p v-else-if="!loading" class="mt-4 gray3--text">
      A barbearia ainda não possui membros
    </p>
  </section>
</template>

<script>
import AppTitle from '@/components/site/Title'

export default {
  components: { AppTitle },
  props: {
    barbershopId: { required: true }
  },
  data: () => ({
    members: [],
    loading: true
  }),
  mounted () {
    this.getBarbershopMembers()
  },
  methods: {
    getBarbershopMembers () {
      this.$axios(`${this.$url_api}/barbershop/${this.barbershopId}/members`)
      .then(response => {
        if (Number(response.status) === 200) {
          if (Number(response.data.codigo) === 200) {
            this.members = response.data.corpo
          }
        }
      }).finally(() => this.loading = false)
    },
    getImage (image) {
      return `${this.$url_backend}${image}`
    }
  }
}
</script>

<style scoped>
ul { display: flex; }
li { display: flex; flex-direction: column; align-content: center; max-width: 200px; }
.image-box { width: 100px; height: 100px; border-radius: 50%; overflow: hidden; }
img { width: 100%; }
h3 { font-size: .9rem; }
</style>