<template>
  <div class="barbershop-detail">
    <div class="container my-8">
      <v-row>
        <v-col cols="12" class="pb-0 d-none d-md-flex">
          <app-title :text="barbershopName" />
        </v-col>
        <v-col cols="12" md="8" class="pa-0 pa-sm-3">
          <v-row>
            <v-col v-if="barbershopImage" cols="12" class="pt-0 pt-sm-3">
              <barbershop-image
                :imageUrl="barbershopImage" 
                :barbershopName="barbershop.name"
              />
            </v-col>
            <v-col v-if="description" cols="12" class="d-flex d-md-none mt-2">
              <barbershop-about :text="description" />
            </v-col>
            <v-col cols="12" class="mt-4">
              <barbershop-services 
                v-if="barbershopId"
                :barbershopId="barbershopId"
                @schedule="schedule"
              />
            </v-col>
            <v-col cols="12" class="mt-4">
              <barbershop-members 
                v-if="barbershopId"
                :barbershopId="barbershopId"
              />
            </v-col>
          </v-row>
        </v-col>
        <v-spacer></v-spacer>
        <v-col cols="12" md="4" class="pa-0 pa-sm-3">
          <v-row>
            <v-col v-if="map" cols="12" class="d-none d-md-flex">
              <barbershop-map 
                :address="address"
                :map="map"
                :barbershopName="barbershopName"
              />
            </v-col>
            <v-col v-if="description" cols="12" class="d-none d-md-flex">
              <barbershop-about :text="description" />
            </v-col>
            <v-col v-if="barbershop" cols="12" sm="6" md="12" class="pt-md-0">
              <app-title text="Contatos" />
              <barbershop-contact 
                :phoneNumber="barbershop.phone_number"
                :instagramUrl="barbershop.instagram_url"
                :facebookUrl="barbershop.facebook_url"
              />
              <v-divider class="mt-2 mb-0 gray3 d-none d-md-flex" :height="2"></v-divider>
            </v-col>
            <v-col cols="12" sm="6" md="12">
              <app-title text="Horários" />
              <schedules :scheduleDays="scheduleDays" />
            </v-col>
            <v-col v-if="map" cols="auto" sm="6" class="mt-4 mx-auto mx-sm-0 d-flex d-md-none">
              <barbershop-map 
                :address="address"
                :map="map"
                :barbershopName="barbershopName"
              />
            </v-col>
          </v-row>
        </v-col>
      </v-row>
    </div>
    <create-schedule-dialog
			ref="createScheduleDialog" 
			:barbershopId="barbershopId"
			:userId="userId"
		/>
    <loader ref="loader" />
  </div>
</template>

<script>
import AppTitle from '@/components/site/Title'
import BarbershopAbout from '@/components/site/barbershop/About'
import BarbershopContact from '@/components/site/barbershop/Contact'
import BarbershopImage from '@/components/site/barbershop/Image'
import BarbershopMap from '@/components/site/barbershop/Map'
import BarbershopMembers from '@/components/site/barbershop/Members'
import BarbershopServices from '@/components/site/barbershop/Services'
import CreateScheduleDialog from '@/components/site/schedule/RegisterSchedule'
import Loader from '@/components/site/Loader'
import Schedules from '@/components/site/barbershop/Schedules'

import UserMixin from '@/mixins/UserMixin'

export default {
  components: { 
    AppTitle, 
    BarbershopAbout, 
    BarbershopContact, 
    BarbershopImage, 
    BarbershopMap,
    BarbershopMembers,
    BarbershopServices,
    CreateScheduleDialog,
    Loader, 
    Schedules 
  },
  mixins: [ UserMixin ],
  data: () => ({
    barbershop: null,
    barbershopId: null,
    serviceId: null
  }),
  computed: {
    address () {
      return this.barbershop ? this.barbershop.address : null
    },
    barbershopImage () {
      return this.barbershop ? this.barbershop.background_url : ''
    },
    barbershopName () {
      return this.barbershop ? this.barbershop.name : ''
    },
    description () {
      return this.barbershop ? this.barbershop.description : null
    },
    map () {
      return this.barbershop ? this.barbershop.address.map_url : null
    },
    scheduleDays () {
      return this.barbershop ? this.barbershop.schedule_days : []
    },
    userId () {
			return this.userState ? this.userState.id : null 
		}
  },
  mounted () {
    this.barbershopId = this.$route.params.id
		this.getBarbershopByIdOnService(this.barbershopId)
  },
  methods: {
    getBarbershopByIdOnService (barbershopId) {
      this.$refs.loader.show()
      this.$axios(`/barbershop/${barbershopId}`)
      .then(response => {
        if (Number(response.status) === 200) {
          if (Number(response.data.codigo) === 200) {
            this.onGetBarbershopByIdOnService(response.data.corpo)
          }
        }
      }).finally(() => this.$refs.loader.hide())
    },
    onGetBarbershopByIdOnService (barbershop) {
      this.barbershop = barbershop
      if (barbershop.name) {
        document.title = barbershop.name
        setTimeout(() => {
          this.$route.meta.pageName = barbershop.name
          window.console.log('aqui: ' + this.$route.meta.pageName)
        }, 300);
      }
    },
    schedule (serviceId) {
      if (this.isUserAutenticated()) this.$refs.createScheduleDialog.showDialog(serviceId)
			else { 
				localStorage.routePathSite = window.location.pathname
				this.goTo('login') 
			}
    }
  }
}
</script>