<template>
  <section class="map elevation-1">
    <div v-html="map"></div>
    <div class="address">
      <img src="/barbershop/tesoura.svg" />
      <div class="detail">
        <h1 class="ma-a gray3--text font-weight-bold">
          {{ barbershopName }}
        </h1>
        <p class="ma-0 gray3--text">
          {{ addressCmpt }}
        </p>
      </div>
      <v-icon></v-icon>
    </div>
  </section>
</template>

<script>
export default {
  props: {
    address: { required: true },
    barbershopName: { required: true },
    map: { required: true }
  },
  computed: {
    addressCmpt () {
      let address = this.address.public_place
      if (this.address.number) address += `, ${this.address.number}`
      address += ` - ${this.address.neighborhood}`
      return address
    }
  }
}
</script>

<style lang="scss" scoped>
.map {
  width: 100%;
  overflow: hidden;
  border-radius: 5px;
  background: #fff;
}

.address {
  padding: 10px 15px;
  display: flex;
  
  img {
    margin-right: 10px;
  }
}

h1 { font-size: .9rem; }
p { font-weight: 300; font-size: .8rem; }
</style>